import React from "react";
import classes from "./FeedBack.module.css";
import FeedBackForm from "./FeedBackForm";

const FeedBack = ({sendFeedBack, feedBackStatus, setFeedBackStatus}) => {

    const handleSubmit = (e) => {
        e.preventDefault(); //      (  )
        const injectedData = { //   ,    
        };

        const inputs = e.target.elements; //      'e'
        const data = {}; //    data
        for (let i = 0; i < inputs.length; i++) { //   data      
            if (inputs[i].name) {
                data[inputs[i].name] = inputs[i].value;
            }
        }
        Object.assign(data, injectedData); //        ,  

        sendFeedBack(data)
    };

    if (feedBackStatus) { //   feedback  BLL  
        return (
            <>
                <div>!</div>
                <div>{feedBackStatus}</div>  {/* */}
                <button onClick={()=>{setFeedBackStatus("")}}> </button> {/*    */}
            </>
        );
    }

    return (
        <div className={classes.feedBackGreed}> {/*      */}
            <div><FeedBackForm
                sendFeedBack={sendFeedBack}
            /></div>
        </div>
    );
};

export default FeedBack;
