import React from "react";
import './App.css';
import {HashRouter} from "react-router-dom";
import HeaderContainer from "./components/Header/HeaderContainer";
import NavBarContainer from "./components/Navbar/NavBarContainer";
import {connect} from "react-redux";
import {initialisedAppThunkCreator} from "./redux/app-reducer";
import Preloader from "./components/common/Preloader/Preloader";
import InfoContainer from "./components/Info/InfoContainer";
import ContentContainer from "./components/Content/ContentContainer";
import FeedBack from "./components/FeedBack/FeedBack";

class App extends React.Component { //  app      
    componentDidMount() {
        this.props.initialisedAppThunkCreator() //   
    }

    render() {
        if (!this.props.initialisedApp) { //     
            return <Preloader/> //   
        }
        return ( //    
            <HashRouter> {/*BrowserRouter  , HashRouter  gh-pages*/}
                <div className={this.props.info_mode?'app-wrapper':'app-wrapper-no-info'}> {/*   */}
                    {/*    <ErrorBoundary>      */}
                    <HeaderContainer/> {/*Header    day/night */}
                    <div className='app-wrapper-navbar'>
                        <NavBarContainer/> {/*     FriendList*/}
                    </div>
                    {this.props.info_mode
                        ?<div className='app-wrapper-info'>
                            <InfoContainer/> {/*        URL*/}
                        </div>
                        : null
                    }
                    <div className='app-wrapper-content'>
                        <div><ContentContainer/> {/*     URL*/}</div>
                    </div>
                    {/*
                    </ErrorBoundary>
*/}
                </div>
            </HashRouter>

        );
    }
}

const mapStateToProps = (state) => {
    return {
        initialisedApp: state.app.initialisedApp, //   
        info_mode: state.app.info_mode, //      
    }
}

export default connect(mapStateToProps, {initialisedAppThunkCreator})(App);
//   app    
