import React from 'react';
import {connect} from "react-redux";
import {postFeedBackThunkCreator2, setFeedBackStatus} from "../../redux/feedback-reducer";
import FeedBack from "./FeedBack";

class FeedBackContainer extends React.Component {

    sendFeedBack = (data) => {
        this.props.postFeedBackThunkCreator2(data)//    api
    }

    render () {
        return <div>
            <FeedBack
                sendFeedBack={this.sendFeedBack} //     
                feedBackStatus={this.props.feedBackStatus} //    BLL
                setFeedBackStatus={this.props.setFeedBackStatus} //   (    )
            />
        </div>
    }
}

let mapStateToProps = (state) => {
    return {
        feedBackStatus: state.feedback.feedBackStatus //     
    }
}

export default connect(mapStateToProps,
    {postFeedBackThunkCreator2, setFeedBackStatus})(FeedBackContainer);
